<template>
  <div id="body">
    <div id="container">
      <div class="item"></div>
    </div>
    <button @click="setNavigation">
      click me
    </button>
      <div>{{localNavigation}}</div>

  </div>
</template>

<script>
import Sortable from 'sortablejs'
import {SETNAVIGATIONHISTORY} from '../../store/mutation-types.js'
export default {
  data() {
    return {
      resizeable: false, // 
      clientX: 0,
      clientY: 0, // 
      minW: 8,
      minH: 8, // div
      direc: '', // nswe
    }
  },
  mounted() {
  },
  methods: {
    setNavigation() {
      this.$store.commit(SETNAVIGATIONHISTORY, 'bb') // commitvuexlocalstorage
      let a = this.$store.state.localstorage // vuexlocalstorage
      console.log(a)
    }
  },
  computed: {
    localNavigation() {
      return this.$store.state.localstorage.localstorage.navigation.navigationHistory
    }
  }
}
</script>

<style scoped>
</style>



